import { Box, Button, IconButton, Typography } from '@mui/material'
import CloseIcon from '@mui/icons-material/Close';
import React, { useEffect } from 'react'
import { hideBodyOverflow } from '../utils/utils';

export default function Privacy ({privacyIsOpen, closeContactForm, closePrivacy}) {

  useEffect(() => {
    hideBodyOverflow(privacyIsOpen)
  }, [privacyIsOpen])
  return (
    privacyIsOpen && (
      <Box sx={{ overflowY:'scroll', position:'fixed', top:'0', left:'0', maxHeight:'100vh', width:'100%', height:'100%', bgcolor:'white', zIndex:'5000',flexDirection:'column', display:'flex',  alignItems:'center'}}>
        <IconButton 
        sx={{
            position:{xs:'static', md:'absolute'}, 
            width:'40px', 
            top:'5%', 
            right:'5%',
            marginTop:'1rem', 
            height:'40px', 
            backgroundColor:'white',
            display:'flex',
            justifyContent:'center',
            alignItems:'center',
            marginBottom:'1rem',
            
        }}
        size='small'
        color='primary'
        variant="outlined" 
        label="close"
        onClick={closePrivacy}
        >
            
            <CloseIcon id='close-icon' sx={{color:'black'}}/>
            </IconButton>
        <Typography variant="h4" color="text.primary" sx={{textAlign:'center', margin:'3rem 0rem 3rem 0rem', fontWeight:'800' }}>개인정보 처리방침</Typography>
        <hr style={{width:'70%', height:'3px', color:'red'}}/>  
        <Box sx={{mt:'2rem', mb:'6rem', width:'85%', textAlign:'justify', display:'flex', flexDirection:'column', justifyContent:'center' , alignItems:'center' }}>
          <Box className="member_wrapper">
          <Box sx={{fontSize:'12px'}}className="privacy_body">
              <b>1. 개인정보의 처리 목적</b><br/><br/>
              (주) 오앤에스는 이하 '(주) 오앤에스 웹 사이트')은(는) 개인정보를 다음의 목적을 위해 처리합니다. 처리한 개인정보는 다음의 목적이외의 용도로는 사용되지 않으며 이용 목적이 변경될 시에는 사전동의를 구할 예정입니다.<br/>
            <br/>
            <b>가. 홈페이지 회원가입 및 관리</b><br/><br/>
            각종 고지·통지, 고충처리, 입사지원관련 등을 목적으로 개인정보를 처리합니다.<br/>
            <br/>
            <b>나. 민원사무 처리</b><br/><br/>
            민원인의 신원 확인, 민원사항 확인, 사실조사를 위한 연락·통지, 처리결과 통보 등을 목적으로 개인정보를 처리합니다.<br/>
            <br/>
            <br/>
            <b>2. 개인정보 파일 현황</b><br/><br/>
            - 개인정보 파일명 : (주) 오앤에스 개인정보처리방침<br/>
            - 개인정보 항목 : 자택주소, 비밀번호, 생년월일, 자택전화번호, 휴대전화번호, 이름, 이메일, 접속 IP 정보<br/>
            - 수집방법 : 홈페이지<br/>
            - 보유근거 : 관련법령의거<br/>
            - 보유기간 : 3년<br/>
            - 관련법령 : 소비자의 불만 또는 분쟁처리에 관한 기록 : 3년, 신용정보의 수집/처리 및 이용 등에 관한 기록 : 3년<br/>
            <br/>
            <br/>
            <b>3. 개인정보처리 위탁</b><br/>
            <br/>
            ① &lt;(주) 오앤에스&gt;('(주) 오앤에스 웹 사이트')은(는) 원활한 개인정보 업무처리를 위하여 다음과 같이 개인정보 처리업무를 위탁하고 있습니다.<br/>
            ② &lt;(주) 오앤에스&gt;( 이하 '(주) 오앤에스 웹 사이트')은(는) 위탁계약 체결시 개인정보 보호법 제25조에 따라 위탁업무 수행목적 외 개인정보 처리금지, 기술적·관리적 보호조치, 재위탁 제한, 수탁자에 대한 관리․감독, 손해배상 등 책임에 관한 사항을 계약서 등 문서에 명시하고, 수탁자가 개인정보를 안전하게 처리하는지를 감독하고 있습니다.<br/>
            ③ 위탁업무의 내용이나 수탁자가 변경될 경우에는 지체없이 본 개인정보 처리방침을 통하여 공개하도록 하겠습니다.<br/>
            <br/>
            <br/>
            <b>4. 정보주체의 권리,의무 및 그 행사방법 이용자는 개인정보주체로서 다음과 같은 권리를 행사할 수 있습니다.<br/>
            </b><br/><br/>
            ① 정보주체는 (주) 오앤에스 이하 (주) 오앤에스 웹 사이트') 에 대해 언제든지 다음 각 호의 개인정보 보호 관련 권리를 행사할 수 있습니다.<br/>
            <br/>
            1. 개인정보 열람요구<br/>
            2. 오류 등이 있을 경우 정정<br/>
            3. 삭제요구<br/>
            4. 처리정지 요구<br/>
            <br/>
            ② 제1항에 따른 권리 행사는(주) 오앤에스 이하 (주) 오앤에스 웹 사이트') 에 대해 개인정보 보호법 시행규칙 별지 제8호 서식에 따라 서면, 전자우편, 모사전송(FAX) 등을 통하여 하실 수 있으며 (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 이에 대해 지체 없이 조치하겠습니다.<br/>
            ③ 정보주체가 개인정보의 오류 등에 대한 정정 또는 삭제를 요구한 경우에는 (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 정정 또는 삭제를 완료할 때까지 당해 개인정보를 이용하거나 제공하지 않습니다.<br/>
            ④ 제1항에 따른 권리 행사는 정보주체의 법정대리인이나 위임을 받은 자 등 대리인을 통하여 하실 수 있습니다. 이 경우 개인정보 보호법 시행규칙 별지 제11호 서식에 따른 위임장을 제출하셔야 합니다.<br/>
            <br/>
            <br/>
            <b>5. 처리하는 개인정보의 항목 작성</b><br/><br/>
            <br/>
            ① (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 다음의 개인정보 항목을 처리하고 있습니다.<br/>
            <br/>
            &lt;홈페이지 회원가입 및 관리&gt;<br/>
            - 필수항목 : 개인정보의 처리 목적, 개인정보파일 현황, 개인정보의 처리 및 보유 기간, 정보주체의 권리·의무 및 그 행사방법에 관한 사항, 처리하는 개인정보의 항목, 개인정보의 파기에 관한 사항, 개인정보 보호책임자에 관한 사항, 개인정보 처리방침의 변경에 관한 사항, 개인정보의 안전성 확보조치에 관한 사항<br/>
            - 선택항목 : 없음<br/>
            <br/>
            <br/>
            <b>6. 개인정보의 파기</b><br/><br/>
            <br/>
            (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 원칙적으로 개인정보 처리목적이 달성된 경우에는 지체없이 해당 개인정보를 파기합니다. 파기의 절차, 기한 및 방법은 다음과 같습니다.<br/>
            <br/>
            <br/>
            -파기절차<br/>
            이용자가 입력한 정보는 목적 달성 후 별도의 DB에 옮겨져(종이의 경우 별도의 서류) 내부 방침 및 기타 관련 법령에 따라 일정기간 저장된 후 혹은 즉시 파기됩니다. 이 때, DB로 옮겨진 개인정보는 법률에 의한 경우가 아니고서는 다른 목적으로 이용되지 않습니다.<br/>
            <br/>
            -파기기한<br/>
            이용자의 개인정보는 개인정보의 보유기간이 경과된 경우에는 보유기간의 종료일로부터 5일 이내에, 개인정보의 처리 목적 달성, 해당 서비스의 폐지, 사업의 종료 등 그 개인정보가 불필요하게 되었을 때에는 개인정보의 처리가 불필요한 것으로 인정되는 날로부터 5일 이내에 그 개인정보를 파기합니다.<br/>
            <br/>
            -파기방법<br/>
            전자적 파일 형태의 정보는 기록을 재생할 수 없는 기술적 방법을 사용합니다. 종이에 출력된 개인정보는 분쇄기로 분쇄하거나 소각을 통하여 파기합니다.<br/>
            <br/>
            <br/>
            <b>7. 개인정보의 안전성 확보 조치</b><br/><br/>
            <br/>
            (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 개인정보보호법 제29조에 따라 다음과 같이 안전성 확보에 필요한 기술적/관리적 및 물리적 조치를 하고 있습니다.<br/>
            <br/>
            <br/>
            1. 개인정보 취급 직원의 최소화 및 교육<br/>
            개인정보를 취급하는 직원을 지정하고 담당자에 한정시켜 최소화 하여 개인정보를 관리하는 대책을 시행하고 있습니다.<br/>
            <br/>
            2. 정기적인 자체 감사 실시<br/>
            개인정보 취급 관련 안정성 확보를 위해 정기적(분기 1회)으로 자체 감사를 실시하고 있습니다.<br/>
            <br/>
            3. 내부관리계획의 수립 및 시행<br/>
            개인정보의 안전한 처리를 위하여 내부관리계획을 수립하고 시행하고 있습니다.<br/>
            <br/>
            4. 개인정보의 암호화<br/>
            이용자의 개인정보는 비밀번호는 암호화 되어 저장 및 관리되고 있어, 본인만이 알 수 있으며 중요한 데이터는 파일 및 전송 데이터를 암호화 하거나 파일 잠금 기능을 사용하는 등의 별도 보안기능을 사용하고 있습니다.<br/>
            <br/>
            5. 해킹 등에 대비한 기술적 대책<br/>
            (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은 해킹이나 컴퓨터 바이러스 등에 의한 개인정보 유출 및 훼손을 막기 위하여 보안프로그램을 설치하고 주기적인 갱신·점검을 하며 외부로부터 접근이 통제된 구역에 시스템을 설치하고 기술적/물리적으로 감시 및 차단하고 있습니다.<br/>
            <br/>
            6. 개인정보에 대한 접근 제한<br/>
            개인정보를 처리하는 데이터베이스시스템에 대한 접근권한의 부여,변경,말소를 통하여 개인정보에 대한 접근통제를 위하여 필요한 조치를 하고 있으며 침입차단시스템을 이용하여 외부로부터의 무단 접근을 통제하고 있습니다.<br/>
            <br/>
            7. 접속기록의 보관 및 위변조 방지<br/>
            개인정보처리시스템에 접속한 기록을 최소 6개월 이상 보관, 관리하고 있으며, 접속 기록이 위변조 및 도난, 분실되지 않도록 보안기능 사용하고 있습니다.<br/>
            <br/>
            8. 비인가자에 대한 출입 통제<br/>
            개인정보를 보관하고 있는 물리적 보관 장소를 별도로 두고 이에 대해 출입통제 절차를 수립, 운영하고 있습니다.<br/>
            <br/>
            <br/>
            <b>8. 개인정보 보호책임자 작성</b><br/><br/>
            <br/>
            ① (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 개인정보 처리에 관한 업무를 총괄해서 책임지고, 개인정보 처리와 관련한 정보주체의 불만처리 및 피해구제 등을 위하여 아래와 같이 개인정보 보호책임자를 지정하고 있습니다.<br/>
            <br/>
            ▶ 개인정보 보호책임자<br/>
            성명 : 담당자 이름<br/>
            직급 : 담당자 직급<br/>
            연락처 : 담당자 전화번호 및 이메일 주소<br/>
            ※ 개인정보 보호 담당부서로 연결됩니다.<br/>
            <br/>
            <br/>
            ▶ 개인정보 보호 담당부서<br/>
            부서명 : 담당 부서<br/>
            연락처 : 담당 부서 전화번호 및 이메일 주소<br/>
            <br/>
            ② 정보주체께서는 (주) 오앤에스 이하 (주) 오앤에스 웹 사이트) 의 서비스(또는 사업)을 이용하시면서 발생한 모든 개인정보 보호 관련 문의, 불만처리, 피해구제 등에 관한 사항을 개인정보 보호책임자 및 담당부서로 문의하실 수 있습니다. (주) 오앤에스 이하 '(주) 오앤에스 웹 사이트') 은(는) 정보주체의 문의에 대해 지체 없이 답변 및 처리해드릴 것입니다.<br/>
            <br/>
            <br/>
            <b>9. 개인정보 처리방침 변경</b><br/><br/>
            <br/>
            ①이 개인정보처리방침은 시행일로부터 적용되며, 법령 및 방침에 따른 변경내용의 추가, 삭제 및 정정이 있는 경우에는 변경사항의 시행 7일 전부터 공지사항을 통하여 고지할 것입니다.        </Box>
              </Box>
            <Button variant={'outlined'} sx={{ mt:'2rem'}}onClick={closePrivacy}>확인</Button>
            </Box>
    </Box>)
  )
}
